.menu-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  transition: opacity 0.4s ease;
}

.menu-header {
  background-size: cover !important;
  width: 100%;
  color: white;
  z-index: 1;
}

.menu-hc {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 4em 0px;
  font-family: Futura;
}

.menu-title {
  position: relative;
  font-size: 5em;
  font-weight: 700;
  padding: 0px 20px 5px;
  margin-bottom: 15px;
}

.menu-title::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 6px;
  bottom: 0;
  left: 0;
  background-color: white;
}

.menu-intro {
  font-weight: 500;
  font-size: 1.1em;
  width: 45%;
}

.menu-nav {
  display: flex;
  align-items: center;
  width: 100%;
  height: 50px;
  background-color: black;
  color: white;

  position: -webkit-sticky;
  position: sticky;
  top: 54px;
  z-index: 11;
  border-bottom: 1px solid lightgray;
}

.menu-nav-content {
  display: flex;
  flex-wrap: wrap;
  height: 50px;
  overflow: hidden;
  flex: 1;
  align-items: center;
  font-family: proxima-nova;
  padding-left: 2.5%;
  margin-right: 5%;
}

.selected {
  color: #F5AD28 !important;
}

.menu-nav button {
  padding: 15px 0px;
  margin-right: 30px;

  background: none;
  color: inherit;
  border: none;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  white-space: nowrap;
}

.menu-content {
  width: 70%;
  margin-bottom: 80px;
}

.category-dropdown {
  text-align: start;
  margin-right: 2.5%;
  width: 175px;
  display: flex;
  justify-content: flex-end;
}

.section-container {
  text-align: start;
  padding: 40px 0 8px 0;
}

.section-title-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
}

.section-title-container:hover {
  cursor: pointer;
}

.section-title {
  font-size: 20px;
  font-family: Futura;
  font-weight: 400;
  line-height: 1.25;
  letter-spacing: 0.25px;
  text-transform: none;
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 0px;
}

/* Menu Item */
.mic {
  display: flex;
  align-items: flex-end;
  box-sizing: border-box;

  font-family: proxima-nova;
}

.mid {
  position: relative;
  flex: 1;
  overflow: hidden;
  box-sizing: border-box;
}

.mis {
  position: relative;
  z-index: 10;
  padding-right: 4px;
  margin-right: 30px;
  font-size: 1.2rem;
  -webkit-box-direction: normal;
  box-sizing: border-box;

  background-color: rgb(255, 255, 255)
}

.mi-spacing {
  letter-spacing: 4px;
  position: absolute;
  z-index: 1;
  bottom: 1px;
  font-size: 15px;
}

.mip {
  padding-left: 5px;
}

.mi-desc {
  font-family: proxima-nova;
  font-size: 0.85rem;
  margin: 5px 0px 0px;
}

.slide-transition {
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
}

.menu-container footer div p {
  margin: 0px;
  font-size: .8rem;
}

@media (max-width: 1100px) {
  .menu-intro {
    width: 60%;
  }
}

@media (max-width: 700px) {
  .menu-intro {
    width: 80%;
  }

}



